/**
 * @component
 * -ModalEditPost
 * @props
 * id -  id ,
 * isActive - true/false -  ,  
 * closeModal -   
 * description -  
 * photo -  
 * @returns
 *  ModalEditPost -  
 */

import { FC, SyntheticEvent, useState } from "react";
import { Modal } from "../Modal/Modal";
import { IModalEditPost } from "../../types/types";
import { Label } from "../Label/Label";
import { Input } from "../Input/Input";
import styleModalEditPost from "./ModalEditPost.module.css";
import { Button } from "../Button/Button";
import { useEditPostMutation } from "../../service/api/userApi";
export const ModalEditPost: FC<IModalEditPost> = ({
  isActive,
  closeModal,
  id,
  description,
  photo,
}) => {
  const [descr, setDescr] = useState(description);
  const [editPost] = useEditPostMutation();
  const [img, setImg] = useState(photo);

  const submitEditPost = async (e: SyntheticEvent) => {
    e.preventDefault();
    await editPost({ id, description: descr, photo: img }).unwrap();
    closeModal();
  };
  return (
    <Modal isActive={isActive} closeModal={closeModal}>
      <div className={styleModalEditPost.modal}>
        <h2 className={styleModalEditPost.title}> </h2>
        <form onSubmit={submitEditPost} className={styleModalEditPost.form}>
          <Label htmlFor="img" text=" ?" />
          <Input
            value={img}
            onChange={(e) => setImg(e.target.value)}
            id="img"
            type="text"
            placeholder=" URL "
          />
          <Label htmlFor="descr" text=" ?" />
          <Input
            value={descr}
            onChange={(e) => setDescr(e.target.value)}
            id="descr"
            type="text"
            placeholder=" "
          />
          <Button classname={styleModalEditPost.btn} text="" />
        </form>
      </div>
    </Modal>
  );
};
